import React from "react";
import * as footerStyles from '../assets/css/footer.module.css';
import VKLogo from '../assets/icons/vk.inline.svg';
import InstLogo from '../assets/icons/inst.inline.svg';
import FBLogo from '../assets/icons/facebook.inline.svg';

const Footer = () => {


    return(
        <div className={footerStyles.container}>
            <div className={footerStyles.left}>
                <div className={footerStyles.heading}>контакты</div>
                <div className={footerStyles.subtext}>momentsbynadi.ru</div>
                <div className={footerStyles.subtext}>+7 985 225 22 72</div>
            </div>
            <div className={footerStyles.center}>
                <div className={footerStyles.heading}>&copy;MomentsByNadi 2021</div>
            </div>
            <div className={footerStyles.right}>
                <div className={footerStyles.heading}>подписывайся</div>
                <div className={footerStyles.socialbox}>
                    <ul className={footerStyles.sociallist}>
                        <li>
                            <a href="https://vk.com/nadya_pashtepa" className={footerStyles.sociallogo} target="blank">
                                <VKLogo className={footerStyles.sociallogo}/>
                            </a>
                        </li>
                        <li>
                            <a href="https://www.instagram.com/moments_by_nadi/?hl=ru" className={footerStyles.sociallogo} target="blank">
                                <InstLogo className={footerStyles.sociallogo}/>
                            </a>
                        </li>
                        <li>
                            <a href="https://facebook.com/Moments-by-Nadi-119616386096441/?ref=bookmarks" className={footerStyles.sociallogo} target="blank">
                                <FBLogo className={footerStyles.sociallogo}/>
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>

    );
};

export default Footer;