import React from "react";
import * as headerStyles from "../assets/css/header.module.css";

const Header = () => {

    return(
        <div className={headerStyles.container}>
                <div className={headerStyles.logo}>
                    <a href="/" className={headerStyles.link}>
                        <span className={headerStyles.text}>
                            Moments by Nadi
                        </span>
                    </a>
                  </div>
                  <div className={headerStyles.menubox}>
                      <ul className={headerStyles.menulist}>
                          <li><a className={`${headerStyles.link} ${headerStyles.navitem}`} href="/about">обо мне</a></li>
                          <li><a className={`${headerStyles.link} ${headerStyles.navitem}`} href="/portfolio">портфолио</a></li>
                          <li><a className={`${headerStyles.link} ${headerStyles.navitem}`} href="/pricing">услуги и цены</a></li>
                          <li><a className={`${headerStyles.link} ${headerStyles.navitem}`} href="/contact">контакты</a></li>
                      </ul>
                  </div>
                  <div className={headerStyles.separator}/>
        </div>
    );
};

export default Header;