import React from "react";
import * as aboutStyles from "../assets/css/about.module.css";
import LeftImage from "../assets/images/nadya.jpg";
import RightImage from "../assets/images/nadya2.jpg";
import InstLogo from '../assets/icons/inst2.inline.svg';

const About = () => {

    return(
        <div className={aboutStyles.container}>
            <div className={aboutStyles.mainsection}>
                <div className={aboutStyles.imagecontainer}>
                    <img src={LeftImage} alt="nadya1" />
                </div>
                <div className={aboutStyles.textcontainer}>
                    <p className={aboutStyles.heading}>Обо мне</p>
                    <p className={aboutStyles.text}>Всем привет! Меня зовут Надежда Петухова, я живу и фотографирую в 
                        Москве и Подмосковье. Добро пожаловать на мой личный сайт! Здесь вы увидите 
                        примеры моих работ в разных жанрах, сможете найти для себя идеи съёмок, узнаете обо 
                        всех условиях работы со мной!
                    </p>
                    <p className={aboutStyles.text}>Я всегда показываю на фотографии людей с их настоящей стороны: искренними и живыми. 
                        Каждая фотография должна вызывать эмоции. Люблю портреты, они раскрывают душу модели, а также позволяют 
                        человеку побыть в образе, в котором он никогда не был:) Для меня очень важен индивидуальный подход к каждому 
                        взрослому и ребёнку, 
                        возможность подчеркнуть его неповторимость.
                    </p>
                    <p className={aboutStyles.text}>У меня есть двое детей,  поэтому мне легче понять маму, найти общий язык и расположить
                         к себе малыша. Именно благодаря своей семье я полюбила детскую и семейную фотографию.
                    </p>
                    <p className={aboutStyles.text}>Фотография для меня всегда была попыткой остановить неуловимое, 
                        заметить незамеченное, но очень ощутимое в душе! Способом остановить 
                        ценные моменты жизни не только для вас, но и для будущих поколений! 
                        Много лет спустя, просматривая фото, наши внуки скажут огромное спасибо 
                        за те тёплые и счастливые воспоминания, которые остались на фотобумаге.
                    </p>
                    <p className={aboutStyles.text}>Я прошла обучение в Школе Фотографии и постоянно совершенствую своё 
                        видение кадра, знания и навыки на различных курсах Фотографии.
                    </p>

                </div>
                <div className={aboutStyles.imagecontainer}>
                    <img src={RightImage} alt="nadya2" />
                </div>
            </div>
            <div className={aboutStyles.bottomSection}>
                <div className={aboutStyles.bottomSectionContent}>
                    <span className={aboutStyles.bottomSubText}>Если хотите узнать больше о моём творческом пути, <br/>
                        добро пожаловать ко мне в </span>
                    <span className={aboutStyles.bottomHeading}>INSTAGRAM</span>
                    <div className={aboutStyles.logoContainer}>
                        <a href="https://www.instagram.com/moments_by_nadi/?hl=ru" className={aboutStyles.logolink} 
                        target="blank">
                            <InstLogo className={aboutStyles.logo}/>
                        </a>
                    </div>
                </div>
                <div className={aboutStyles.bottomSection}>
                    <div className={aboutStyles.bigShader}></div>
                    <div className={aboutStyles.smallShader}></div>
                </div>
            </div>
        </div>
    );
};

export default About;